.spinner {
    margin-right: 5px;
}
.circle {
	fill: none;
	stroke: #1e4164;
	stroke-width: 3;
	stroke-linecap: round;
	stroke-miterlimit: 10;
}
.circle-dash {
	fill: none;
	stroke: #1e4164;
	stroke-width: 3;
	stroke-linecap: round;
	stroke-miterlimit: 10;
}
.check, .cross {
	fill: none;
	stroke: #1e4164;
	stroke-width: 3;
	stroke-linecap: round;
	stroke-linejoin: round;
	stroke-miterlimit: 10;
}
.check-dash, .cross-dash {
	fill: none;
	stroke: #1e4164;
	stroke-width: 3;
	stroke-linecap: round;
	stroke-linejoin: round;
	stroke-miterlimit: 10;
}
.loading .check, 
.loading .cross,
.loading .check-dash, 
.loading .cross-dash,
.failed .check, 
.success .cross,
.failed .check-dash, 
.success .cross-dash {
    display: none;
}

.success .check, .failed .cross {
	stroke-dasharray: 60 100;
	animation: check 0.8s cubic-bezier(0.5, 0, 0.6, 1) forwards 150ms;
	opacity: 0;
}

@keyframes check {
	from {
		stroke-dashoffset: 60;
		opacity: 1;
	}

	to {
		stroke-dashoffset: 00;
		opacity: 1;
	}
}

.success .check-dash, .failed .cross-dash {
	stroke-dasharray: 10 100;
	animation: check-dash 800ms cubic-bezier(0.5, 0, 0.6, 1) forwards 50ms;
}

@keyframes check-dash {
	from {
		stroke-dashoffset: 120;
	}
	to {
		stroke-dashoffset: 45;
	}
}

.loading .circle {
	stroke-dasharray: 300 300;
	animation: circle 1s cubic-bezier(0.5, 0, 0.5, 1) forwards infinite 150ms;
	opacity: 0;
}

@keyframes circle {
	from {
		stroke-dashoffset: 300;
		opacity: 1;
	}
	to {
		stroke-dashoffset: 0;
		opacity: 1;
	}
}

.loading .circle-dash {
	stroke-dasharray: 10 300;
	animation: circledash 1s cubic-bezier(0.5, 0, 0.5, 1) forwards infinite 50ms;
	opacity: 0;
}

@keyframes circledash {
	from {
		stroke-dashoffset: 320;
		opacity: 1;
	}
	to {
		stroke-dashoffset: 20;
		opacity: 1;
	}
}